html {
  box-sizing: border-box;
}

*, 
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0; 
  background-color: rgba(7,7,34,255);
  background-image: url(/planetsfactsite/static/media/background-stars.914faf16.svg);
}

.container {
  max-width: 1111px;
  margin: 0 auto;
}
:root {
  --veryLightGray :rgb(212,213,224);
  --darkGray: rgb(68,67,83);
  --nav-background: rgba(7,7,34,255);
  --white: rgb(255, 255, 255);
  --circle: rgba(45,104,240,255)
}

body {
  font-family: 'Antonio', sans-serif;
}

header {
  border-bottom: 1px solid rgb(212,213,224);
  border-bottom: 1px solid var(--veryLightGray);
  position: relative;
  padding: 1em 1.25em;
}

.header-flex {
  display: flex;
  flex-direction: column;
}

.logo {
  text-transform: uppercase;
  color: rgb(255, 255, 255);
  color: var(--white);
  margin: 0;
}

.nav-toggle{
  display: none;
}

.nav-toggle-label{
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  display: flex;
  align-items: center;
  cursor: pointer;
  
}

.nav-toggle-label span,
.nav-toggle-label span::before,
.nav-toggle-label span::after{
  display: block;
  background: rgb(255, 255, 255);
  background: var(--white);
  height: 4px;
  width: 1.75em;
  margin-right: 1.25em;
  position: relative;
}

.nav-toggle-label span::before,
.nav-toggle-label span::after{
  content: '';
  position: absolute;
}

.nav-toggle-label span::before{
  bottom: 8px;
}

.nav-toggle-label span::after{
  top: 8px;
}

.nav{
  position: absolute;
  top: 102%; 
  left: 0;
  right: 0;
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  -webkit-transform-origin: left;
          transform-origin: left;
  transition: -webkit-transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out, -webkit-transform 400ms ease-in-out;
  background-color: rgba(7,7,34,255);
  background-color: var(--nav-background);
}

.nav-list-flex {
  display: flex;
  flex-direction: column;
}

.nav-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-list-item {
  border-bottom: 1px solid rgb(68,67,83);
  border-bottom: 1px solid var(--darkGray);
  cursor: pointer;
}


.nav-list-item-btn {
  font-family: 'Spartan', sans-serif;
  font-weight: 700;
  color: rgb(212,213,224);
  color: var(--veryLightGray);
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: .9rem;
  padding: 1.45em .5em;
  width: 100%;
  text-align: left;
  background: none;
  border: none;
  cursor: pointer;
  opacity: 0;
  transition: opacity 150ms ease-in-out;
}

.nav-toggle:checked ~ .nav{
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

.nav-toggle:checked ~ nav .nav-list-item-btn{
  opacity: 1;
  transition: opacity 250ms ease-in-out 250ms;
}

.planet-span {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: rgba(45,104,240,255);
  background: var(--circle);
  display: inline-block;
  position: relative;
  top: 4px;
  margin-left: 1em;
  margin-right: 2em;
}

.planet-chevron-icon {
  float: right;
  margin-right: 1em;
  
}

@media (min-width: 770px) {

  .nav-list-flex {
    flex-direction: row;
  }

  header {
    padding-bottom: 0;
  }

  .logo {
    text-align: center;
  }

  .planet-span,
  .planet-chevron-icon {
    display: none;
  }

  .nav {
    all: unset;
  }

  .nav-toggle-label span{
    display: none;
  }

  .nav-list {
    justify-content: space-between;
  }

  .nav-list-item {
    border: none;
  }

  .nav-list-item-btn {
    opacity: 1;
  }
}

@media screen and (min-width: 1200px) {
  .header-flex {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .nav-list-item {
    margin: 0 1em;
  }
}
:root {
  --veryLightGray :rgb(212,213,224);
  --lightGray: rgb(88,89,115);
  --darkGray: rgb(68,67,83);
  --white: rgb(255, 255, 255);
}

.planet-details {
  margin: 0 20px 30px 20px;
}

.planet-details-flex {
  display: flex;
  flex-direction: column;
  font-family: 'Spartan', sans-serif;
  color: rgb(255, 255, 255);
  color: var(--white);
}

/* Planet Image */
.planet-img {
  order: 2; 
  max-width: 400px;
  max-height: 400px; 
  width: 100%;
  height: auto;
}

.planet-img-div {
  text-align: center; 
}

/* Planet Buttons Styling */
.planet-btn-div {
  order: 0;
}

.planet-btn-div-flex {
  display: flex;
  justify-content: space-between;  
  order: 0;
  border-bottom: 1px solid rgb(212,213,224);
  border-bottom: 1px solid var(--veryLightGray);
  padding: 1em 0;
}

.btn {
  text-transform: uppercase;
  font-family: 'Spartan', sans-serif;
  font-size: 10px;
  font-weight: 600;
  letter-spacing: 3px;
  color: rgb(255, 255, 255);
  color: var(--white);
  background: none;
  border: none;
  cursor: pointer;
}

.planet-info-btn {
  padding: 1em 0;
  margin: 0 1em;
}

/* Planet Content Styling */
.planet-name {
  font-family: 'Antonio', sans-serif;
  text-transform: uppercase;
  font-size: 3.5rem;
  letter-spacing: 2px;
  margin: 0;
}

.planet-name-subtitle {
  line-height: 1.7;
  margin: 2em 0; 
  color: rgb(212,213,224); 
  color: var(--veryLightGray);
}

.planet-wiki-external-link,
.planet-wiki-external-link-icon {
  color: rgb(68,67,83);
  color: var(--darkGray);
}

.planet-wiki-external-link {
  margin: 0 5px; 
  font-weight: 700;
}

.planet-content {
  order: 1;
  text-align: center;
}

.planet-content-btn-div {
  display: none;
}


/* Tablet screens */
@media (min-width: 770px) {
  .planet-btn-div {
    display: none;
  }

  .planet-content-flex {
    display: flex;
    justify-content: space-between;
  }

  .planet-content {
    text-align: left;
    margin-left: 10px;
    margin-right: 10px;
  }

  .planet-content-div {
    width: 50%;
  }

  .planet-content-btn-div {
    display: block;
    width: 42%; 
  }

  .planet-content-btn-flex {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .planet-content-btn {
    border: 1px solid rgb(88,89,115);
    border: 1px solid var(--lightGray);
    text-align: left;
    padding: 2em;
    margin-bottom: 2em;
  }

  .planet-content-btn span {
    margin-right: 2em;
    color: rgb(88,89,115);
    color: var(--lightGray);
  }
}

/* Desktop and big screens */
@media screen and (min-width: 1200px) {
  .planet-details-flex {
    flex-direction: row;
  }

  .planet-content-btn {
    width: 100%;
  }

  .planet-content-btn-div {
    width: 50%;
  }

  .planet-content-flex {
    flex-direction: column;
    align-items: flex-end;
  }

  .planet-img-div {
    display: flex;
    align-items: center;
  }

  .planet-img {
    width: 400px;
    height: 400px; 
  }

  .planet-content-flex > * {
    width: 70%;
  }

  .planet-details {
    margin-top: 5em;
    margin-bottom: 2em;
  }

  .wiki-link-div {
    margin-bottom: 2em;
  }

}

